<template>
  <div class="contents-container">    
    showPlantListAllYn : {{ showPlantListAllYn }}
  </div>
  <div class="contents-container">
    <PlantList
                v-bind:plantList="dataPlantList" 
                      :dataKeywordPlantList="dataKeywordPlantList"
                      :plantKeywordClicked="plantKeywordClicked"
                v-on:plantClicked="showPlantContent"
                v-on:setAllPlantList="showAllPlantList"
                v-on:sendKeywordPlantList="showKeywordPlantList">
    </PlantList>
  </div>
  <div class="contents-container" >
    <PlantListPreview v-if="showPlantListAllYn"
                v-bind:plantList="dataPlantList" 
                v-on:plantClicked="showPlantContent">
    </PlantListPreview>
    <PlantContent v-else
                    v-bind:plantInfo="paramPlantInfo"
                          :dataKeywordPlantList="dataKeywordPlantList"
                    v-on:sendKeywordPlantList="showKeywordPlantList">
    </PlantContent>

  </div>
</template>

<script>
import PlantList from './PlantList.vue';
import PlantListPreview from './PlantListPreview.vue';
import PlantContent from './PlantContent.vue';
// import PlantGuide from './PlantGuide.vue';
import plantListJson from '../../assets/plantList.json'; // select get_result_json(17);
import plantKeywordPlant from '../../assets/plantKeywordPlant.json'; // select get_result_json(27);

const dataKeywordPlantList = plantKeywordPlant;
const dataPlantList = {};
const paramPlantInfo = [];
let showPlantListAllYn = true;
let plantKeywordClicked = '';

let grwng_min_tmprtr_val = 0;
let grwng_max_tmprtr_val = 0;


export default {
  props: ['plantList'], 
  components: {
    PlantList,
    PlantListPreview,
    PlantContent,
    // PlantGuide,
  },
  data() {
    return {
      dataPlantList, 
      paramPlantInfo,
      dataKeywordPlantList,
      showPlantListAllYn,
      plantKeywordClicked,
      grwng_min_tmprtr_val, 
      grwng_max_tmprtr_val,

    };
  },
  created() {
    this.showAllPlantList();
  },
  mounted() {

  },
  update() {
    // console.log('PlantIntro.vue : update() ... in ');
    // this.showPlantContent(this.plantInfo);
  },
  methods: {
    showPlantContent(plantInfo) {
      console.log('PlantIntro.vue : showPlantContent : plantInfo ::: ', plantInfo);
      // console.log('PlantIntro.vue : showPlantContent() : plantInfo : ', plantInfo);
      this.showPlantListAllYn = false;
      this.paramPlantInfo = plantInfo;

      // console.log('this.paramPlantInfo ::: ', this.paramPlantInfo);
      this.grwng_min_tmprtr_val = plantInfo.grwng_min_tmprtr_val;
      this.grwng_max_tmprtr_val = plantInfo.grwng_max_tmprtr_val;

      // chartData = {
      //   labels: ['최저 온도', '최고 온도'],
      //   datasets: [{
      //         label: '온도 (°C)',
      //         // data: [this.lowestTemperature, this.highestTemperature],
      //         data: [20, 20],
      //         backgroundColor: [
      //           'rgba(255, 99, 132, 0.5)', // 최고 온도 막대의 배경색
      //           'rgba(54, 162, 235, 0.5)' // 최저 온도 막대의 배경색
      //         ],
      //         borderColor: [
      //           'rgba(255, 99, 132, 1)',
      //           'rgba(54, 162, 235, 1)'
      //         ],
      //         borderWidth: 1
      //     }]
      // };
      // chartOptions = { 
      //   scales: {
      //           y: {
      //             beginAtZero: true
      //           }
      //         },
      //   responsive: true 
      // };
      

    },   
    showKeywordPlantList(plantKeywordVw, paramPlantKeyword) {
      // 선택한 키워드코드와 일치하는 작물 목록을 담는다.
      
      const keywordPlantList = {};
      this.dataPlantList = {};
      // this.dataKeywordPlantList = {};
      var dataPlantListTmp = plantListJson;
      var idx = 0;

      for(var i=0; i < paramPlantKeyword.length; i++) {
        for(var j=0; j < dataPlantListTmp.length; j++) {
          // console.log('plantList[i] == dataPlantList[j].plnt_id = ', plantList[i] == dataPlantListTmp[j].plnt_id);
          if (paramPlantKeyword[i].plnt_id == dataPlantListTmp[j].plnt_id) {
            keywordPlantList[idx] = dataPlantListTmp[j];
            idx++;
          }
        }
      }

      this.plantKeywordClicked = plantKeywordVw;
      this.dataPlantList = keywordPlantList;
      // showPlantListAllYn = false; 키워드 클릭 시, 해당 식물 목록들도 사진으로 보이게.
      this.showPlantListAllYn = true; 
    },

    showAllPlantList() {
      this.dataPlantList = plantListJson;
      this.showPlantListAllYn = true;
    }
    
  },
};
</script>