        //           label: '온도 (°C)',
        //           // data: [this.lowestTemperature, this.highestTemperature],
        //           data: [20, 20],
        //           backgroundColor: [
        //             'rgba(255, 99, 132, 0.5)', // 최고 온도 막대의 배경색
        //             'rgba(54, 162, 235, 0.5)' // 최저 온도 막대의 배경색
        //           ],
        //           borderColor: [
        //             'rgba(255, 99, 132, 1)',
        //             'rgba(54, 162, 235, 1)'
        //           ],
        //           borderWidth: 1
        //       }],
        //     },
        //   chartOptions : {
        //     scales : {
        //             y: {
        //               beginAtZero: true
        //             }
        //           },
        //     responsive : true
        //   }

  },
  methods: {
    checkPlantImage() {
      console.log('this.checkPlantImage();');

      if (typeof this.plantInfo.plnt_nm === 'undefined') {
        this.showPlantInfo = false;
      } else {
        this.showPlantInfo = true;

        // console.log('this.plantInfo ::: ', this.plantInfo);

